<template>
  <div class="main-app">
    <Header />
    <main>
      <router-view />
    </main>
    <Footer />
  </div>
</template>

<script>
import axios from "axios";

import Header from "@/components/Header";
import Footer from "@/components/Footer";

export default {
  name: "App",
  components: {
    Header,
    Footer,
  },
  mounted() {
    this.$store.commit("initStore");
    this.setCategories();
  },
  watch: {
    $route(to, from) {
      if (to != from) {
        window.scroll(0, 0);
      }
    },
  },
  methods: {
    async setCategories() {
      await axios
        .get("/api/shop/categories/")
        .then((response) => {
          this.$store.state.categories = response.data;
        })
        .catch((error) => {
          if (error.response) {
            console.log(JSON.stringify(error.response.data));
          }
        });
    },
  },
};
</script>

<style>
main {
  min-height: 80vh;
}
</style>
